.container {
    display: flex;
    width: 100%;
    flex: 1 0 auto;
    height: 0;
    flex-flow: column;
}

.header {
    font-size: 2em;
    font-weight: bold;
    width: 100%;
    padding: 20rem;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.content {
    display: flex;
    flex-flow: row;
    width: 100%;
    height: 0;
    padding: 0 50rem 50rem 50rem;
    justify-content: center;
    gap: 50rem;
    flex: 1 0 auto;
}

.image {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    max-width: 50%;
    height: 100%;
    flex: 0 1 auto;
    aspect-ratio: 1/1;
    overflow: hidden;
}

.image img {
    border-radius: 50rem;
    object-fit:cover;
    max-width: 90%;
    max-height: 90%;
    /* width: clamp(fit-content, fit-content, 100%); */
    /* height: clamp(fit-content, fit-content, 100%);  */
}

.text {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    max-width: 50%;
    min-width: 50%;
    height: 100%;
    text-wrap: pretty;
    text-wrap: wrap;
}